import React from "react"

import IpadProFrame from "../images/ipad_700h.png"

import "./tabletVideo.scss"

const TabletVideo = (props) => {
    return (
        <div className="videoContainer">
            <img src={IpadProFrame} width="100%" alt="Demo Video of Tablet version"></img>
            <video autoPlay loop muted className="tabletvideo">
                <source src={props.video} type="video/mp4"/>
            </video>
        </div>
    )
}

export default TabletVideo