import React from "react"

import Iphone6Frame from "../images/iphone6_700h.png"

import "./phoneVideo.scss"

const PhoneVideo = (props) => {
    return (
        <div className="videoContainer">
            <img src={Iphone6Frame} width="100%" alt="Demo Video of Phone Version"></img>
            <video autoPlay loop muted className="phoneVideo">
                <source src={props.video} type="video/mp4"/>
            </video>
        </div>
    )
}

export default PhoneVideo